/**
 * The entry point of the Chat SDK.
 * @module ChatSDK
 */
import protobuf from 'protobufjs';
import Long from 'long';
import { proto } from './proto';
import { Connection } from './engineCore/connection';
import { Message } from './message/index';
import { utils } from './utils';
import { logger } from './logger';
import { Code } from './status';
protobuf.util.Long = Long;
protobuf.configure();
var root = protobuf.Root.fromJSON(proto);
Connection.prototype.root = root;
var newLogger = logger.getLogger('defaultLogger');
newLogger.setConfig({
    useCache: false,
    maxCache: 3 * 1024 * 1024,
    color: '',
    background: '',
});
newLogger.enableAll();
Connection.prototype.getUniqueId = utils.getUniqueId;
var easemobChat = {
    connection: Connection,
    message: Message,
    utils: utils,
    logger: newLogger,
    statusCode: Code,
};
if (typeof window !== 'undefined') {
    window.WebIM = easemobChat;
}
export default easemobChat;
